.navbar {
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);

    padding: 15px;
    width: 100vw;
    max-width: 1200px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    color: #fff;
    background-color: #f5faf7;
    z-index: 10;
}

.navbar ul {
    list-style-type: none;
    display: flex;
    gap: 10px;
}

.navbar ul li button {
    padding: 0 20px;
    font-size: 14px;

    font-size: 14px;
    text-transform: uppercase;

    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
}

.navbar ul li a {
    padding: 20px 40px;
    font-size: 14px;
    color: #000;
    text-transform: uppercase;
}

.navbar select {
    padding: 10px;

    border: none;
    outline: none;
    background-color: transparent;

    cursor: pointer;
}

.navbar select option {
    padding: 10px;

    border: none;
    outline: none;
    background-color: transparent;

    cursor: pointer;
}

.burger, .remove_menu {
    width: 20px;
    display: none;
    cursor: pointer;
}

.remove_menu {
    position: absolute;
    top: 20px;
    right: 40px;
}

@media only screen and (max-width: 980px) {
    .navbar {
        padding: 15px 20px;
        width: 100vw;
    }

    .navbar ul {
        position: absolute;
        top: -700px;
        left: 0;

        padding: 20px;
        width: 100%;
        flex-direction: column;
        
        background-color: #fff;
        color: #ffffff;
        transition: .4s;

        box-shadow: 0px 3px 21px #0000006b;
    }

    .navbar ul.active {
        top: 0px;
    }

    .navbar ul li {
        padding: 20px;
        width: 100%;
        text-align: center;
    }

    .navbar ul li a {
        padding: 20px;
        font-weight: 500;
        color: #000;
    }
    
    .navbar ul li a,
    .navbar ul li button {
        text-decoration: underline;
    }

    .burger, .remove_menu {
        display: inline;
    }
}

@media (max-width: 300px) {
    .navbar {
        padding: 10px;
    }
}