.check_your_email_wrapper {
    width: 100%;
    height: calc(100vh - 70px);

    display: flex;
    align-items: center;
    justify-content: center;
}

.check_your_email_wrapper h1 {
    font-size: 34px;
    text-align: center;
}

@media (max-width: 500px) {
    .check_your_email_wrapper h1 {
        font-size: 24px;
    }
}

@media (max-width: 300px) {
    .check_your_email_wrapper h1 {
        font-size: 18px;
    }
}