.activate_email_container {
    margin-top: 70px;
    padding: 20px;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.activate_email_container h1 {
    text-align: center;
}

.activate_email_container button {
    margin-top: 20px;
    padding: 10px 30px;
    
    font-size: 13px;
    text-align: center;
    background-color: #00954b;
    color: #fff;

    border: 1px solid #00954b;
    border-radius: 12px;
}

@media (max-width: 500px) {
    .activate_email_container h1 {
        text-align: center;
        font-size: 24px;
    }
}