.login_container {
    padding: 40px;
    margin-top: 80px;
    width: 100%;
    min-height: calc(100vh - 80px);

    display: flex;
    align-items: center;
    flex-direction: column;
}

.login_container .form_header {
    padding: 20px 0;
}

.login_container .reserve_form {
    padding: 40px;
    max-width: 600px;

    background-color: #fff;
}

.login_container .reserve_form.add_car {
    margin: 0 auto;
}

@media (max-width: 500px) {
    .login_container {
        padding: 20px;
    }

    .login_container .reserve_form.add_car {
        padding: 20px;
    }

    .login_container .form_header {
        padding: 0 0 10px;
    }
}


@media (max-width: 300px) {
    .login_container {
        padding: 10px;
    }

    .login_container .reserve_form.add_car {
        padding: 10px;
    }
}

.login__wrapper {
    padding: 20px;
    width: 100vw;
    height: calc(100vh - 70px);

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
}

.login__wrapper .login_form {
    padding: 40px;
    width: 100%;
    max-width: 500px;

    display: flex;
    align-items: stretch;
    justify-content: flex-start;
    flex-direction: column;

    border-radius: 14px;
    box-shadow: 2px 4px 16px #eaeaea;
    background-color: #fff;
}

.login__wrapper .login_form h1 {
    margin-bottom: 20px;
    text-align: center;
}

.login__wrapper .login_form div {
    margin-bottom: 20px;
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
}

.login__wrapper .login_form div input {
    width: 100%;
    padding: 8px;

    outline: none;
    border: 1px solid #c1c1c1;
    border-radius: 8px;
}

.login__wrapper .login_form div input[type='submit'] {
    background-color: #000;
    color: #fff;
    cursor: pointer;
    border: none;

    text-align: center;
}

.login__wrapper .login_form p {
    font-size: 14px;
    text-align: center;
}

.login__wrapper .login_form a {
    font-size: 14px;
    color: #3474ff;
    text-decoration: underline;
}

.login__wrapper .login_form p a {
    color: #3474ff;
    text-decoration: underline;
}


@media (max-width: 500px) {
    .login__wrapper .login_form {
        padding: 20px;
    }

    .login__wrapper .login_form div label {
        font-size: 12px;
    }

    .login__wrapper .login_form p {
        font-size: 12px;
    }
}

@media (max-width: 300px) {
    .login__wrapper {
        padding: 10px;
    }

    .login__wrapper .login_form {
        font-size: 15px;
    }
}