.home__conatiner {
    width: 100vw;
    min-height: 100vh;

    z-index: 20;
}

.main_swiper {
    width: 100%;
    max-width: 1250px;
}

.slider_wrapper {
    padding: 40px;
    width: 100%;
}

.slider {
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
}

.left_slider {
    padding: 40px;
    width: 40%;

    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;

    cursor: grab;
}

.left_slider * {
    margin-bottom: 20px;
}

.left_slider a {
    padding: 10px 40px;
    background-color: #00954B;
    color: #fff;
    border-radius: var(--main-radius);

    cursor: pointer;
}

.right_slider {
    padding: 40px;
    width: 60%;

    display: flex;
    align-items: center;
    justify-content: center;
}

.right_slider img {
    width: 100%;
    max-width: 700px;

    cursor: grab;
}

.some_info {
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 40px;
}

.some_info > div {
    padding: 20px 40px;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    
    
    background-color: #00954B;
    color: #fff;
    border-radius: var(--main-radius);
    box-shadow: 2px 4px 16px #e1e1e1;
}


.some_info > div img {
    margin-right: 10px;
    width: 30px;
}

.some_info > div > div h2 span {
    font-weight: 300;
    font-size: 17px;
}

.home__conatiner .header {
    padding: 20px;
    margin: 0 auto;
    width: 100%;
    max-width: 1250px;
}

.home__conatiner .header h1 {
    text-align: center;
}

.home__conatiner .row {
    margin: 0 auto;
    width: 100%;
    max-width: 1250px;

    display: flex;
    gap: 20px;
}

.home__conatiner .row .col-2 {
    padding: 20px;
    width: 50%;    
}

.home__conatiner .row .col-2 p {
    margin-bottom: 20px;
    font-size: 14px;
}

.home__conatiner .row .col-2 .img_container {
    width: 100%;
    aspect-ratio: 16/9;
    border-radius: 8px;
    box-shadow: 2px 4px 16px #e1e1e1;
    overflow: hidden;
}

.home__conatiner .row .col-2 .img_container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.home__conatiner .row .col-1 {
    padding: 20px 20px 30px;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
}

.home__conatiner .row .col-1 .map_container {
    width: 100%;
    aspect-ratio: 16/9;

    border-radius: 8px;
    box-shadow: 2px 4px 16px #e1e1e1;
    overflow: hidden;
}

.home__conatiner .row .col-1 .map_container iframe {
    width: 100%;
    height: 100%;

    border: none;
    outline: none;
}

.home_btns {
    padding: 12px;
    width: 50px;
    height: 50px;

    display: flex;
    align-items: center;
    justify-content: center;
    
    color: #fff;
    text-align: center;
    font-size: 13px;
    
    background-color: #00954B;
    border: 1px solid #00954B;
    border-radius: 12px;
    box-shadow: 2px 4px 16px #e1e1e1;
}

.home_btns img {
    width: 100%;
}

footer {
    margin: 0 auto;
    width: 100%;
    padding: 10px;

    text-align: center;
    border-top: 1px solid #eaeaea;
}

footer a {
    text-decoration: underline;
}

@media only screen and (max-width: 820px) {
    .slider {
        flex-direction: column-reverse;
    }

    .left_slider, .right_slider {
        width: 100%;
    }

    .some_info {
        gap: 20px;
    }

    .home__conatiner .row {
        flex-direction: column;
    }

    .home__conatiner .row .col-2 {
        width: 100%;
    }
}


@media only screen and (max-width: 620px) {
    .home__conatiner {
        margin-top: 40px;
    }

    .some_info {
        flex-direction: column;
    }

    .slider_wrapper {
        padding: 20px;
    }

    .left_slider, .right_slider {
        padding: 0px;
    }
    
    .right_slider {
        margin-bottom: 20px;
    }

    .some_info > div {
        width: 100%;
    }
}

@media only screen and (max-width: 300px) {
    .slider_wrapper {
        padding: 10px;
    }

    .home__conatiner .row .col-2, .home__conatiner .row .col-1 {
        padding: 15px;
    }

    .home__conatiner .row .col-2 p {
        font-size: 13px;
    }
}