.form_header {
    padding: 10px 20px;
    width: 100%;
    max-width: 600px;

    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.form_header h1 {
    padding-right: 10px;
    min-width: max-content;
    font-size: 24px;
    font-weight: 500;
    color: #7b7b7b;
}

.form_header hr {
    position: relative;
    width: 100%;
    height: 1px;
    background-color: #000;
}

.form__images__container {
    margin-bottom: 10px;
    padding: 10px;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: 10px;

    border-radius: 8px;
    border: 1px solid #afafaf;
}

.form__images__container .img_wrapper {
    position: relative;

    padding: 5px;
    width: 60px;
    height: 60px;

    display: flex;
    align-items: center;
    justify-content: center;

    border: 1px dashed #000;
    border-radius: 6px;
}

.form__images__container .img_wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.form__images__container .img_wrapper span {
    position: absolute;
    top: 5px;
    right: 5px;

    width: 17px;
    height: 17px;

    display: flex;
    align-items: center;
    justify-content: center;

    background-color: #fff;
    border-radius: 50%;
    border: none;

    box-shadow: 1px 1px 6px #c0c0c0;
    cursor: pointer;
}

.form__images__container.main_img {
    padding: 10px;
    border: 1px solid #e1e1e1;
}

.form__images__container.main_img .img_wrapper {
    border: none;
    box-shadow: 2px 2px 10px #dfdfdf;
    cursor: pointer;
}

.form__images__container.main_img .img_wrapper.active {
    border: 1px solid #0bb314;
}