.cars_wrapper {
    margin: 0 auto;
    margin-top: 70px;

    padding: 20px;
    width: 100vw;
    max-width: 1400px;

    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
}

.cars_header {
    margin: 0 auto 20px;
    width: 100%;
    max-width: 1200px;
}

.cars_header h2 {
    font-size: 27px;
    font-weight: 600;
    color: #00954b;
}

.cars_header span {
    font-size: 14px;
    font-weight: 400;
    color: #00954b;
}

.cars_container {
    margin: 0 auto;
    width: 100%;
    max-width: 1200px;

    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: 40px;
}

.car_container {
    margin-bottom: 20px;
    position: relative;
    width: 30%;
    /* max-width: 350px; */

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    background-color: #fff;
    border-radius: 12px;
    box-shadow: 0px 4px 16px #eaeaea;
    overflow: hidden;
}

.car_container .admin_btns {
    padding: 10px;
    position: absolute;
    top: 0;
    right: 0;

    width: 100%;

    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 10px;
}

.car_container .admin_btns a,
.car_container .admin_btns button {
    padding: 10px;
    width: 25px;
    height: 25px;

    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    border: none;
    outline: none;

    cursor: pointer;
}

.car_container .admin_btns a img,
.car_container .admin_btns button img {
    width: 20px;
}

.car_container .admin_btns a {
    background-color: #00954B;
}
.car_container .admin_btns button {
    background-color: #950000;
}


.car_container .img_container {
    width: 100%;
    aspect-ratio: 2/1;

    background-position: center;
    
    border-radius: 8px 8px 0 0;
    overflow: hidden;
}

.car_container .img_container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.car_container .name_and_price {
    padding: 20px;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: space-between;
}


.car_container .name_and_price p {
    width: 100%;
    max-width: 140px;

    font-size: 13px;
    font-weight: 400;
    color: #00954b;
}

.car_container .name_and_price .price_wrapper strong {
    font-size: 17px;
    font-weight: 600;
    color: #00954b;
}

.car_container .name_and_price .price_wrapper span {
    font-size: 12px;
    font-weight: 400;
    color: #00954b;
}

.car_container .car_btns {
    padding: 0 20px 20px;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;

}

.car_container .car_btns a {
    padding: 10px;
    width: 100%;

    background-color: var(--main-color);
    color: #000;
    text-align: center;

    font-size: 13px;

    border-radius: 12px;
}

.car_container .car_btns a:nth-child(1) {
    color: #00954B;
    background-color: #fff;
    border: 1px solid #00954B;
}

.car_container .car_btns a:nth-child(2) {
    color: #fff;
    background-color: #00954B;
    border: 1px solid #00954B;
}

@media only screen and (max-width: 1035px) {
    .car_container {
        width: 48%;
    }
    
    .cars_container {
        justify-content: space-between;
        gap: 20px;
    }
}

@media only screen and (max-width: 720px) {
    .cars_wrapper {
        grid-template-columns: 1fr;
    }
}

@media only screen and (max-width: 300px) {
    .car_container .img_container {
        height: 160px;
    }
}

@media only screen and (max-width: 570px) {
    .car_container {
        width: 100%;
    }
    
    .cars_container {
        justify-content: space-between;
        gap: 20px;
    }
}

@media only screen and (max-width: 300px) {
    .cars_wrapper {
        padding: 10px;
    }

    .car_container .name_and_price {
        padding: 10px;
    }

    .car_container .name_and_price p {
        font-size: 12px;
    }
    
    .car_container .car_btns {
        padding: 0 10px 15px;
    }

    .car_container .car_btns a {
        padding: 8px;
        font-size: 11px;
    }

    .cars_header h2 {
        font-size: 21px;
        line-height: 19px;
    }

    .cars_header span {
        font-size: 12px;
    }
}