.orders_container {
    margin: 70px auto 0;
    padding: 20px;
    width: 100vw;
    max-width: 1200px;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.orders_container h1 {
    margin-bottom: 20px;
}

.orders_container .body {
    width: 100%;
    
    display: flex;
    align-items: stretch;
    justify-content: flex-start;
    gap: 20px;
}

.orders_container .body .car_infos {
    max-width: 400px;
    width: 100%;
    padding: 40px;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    gap: 12px;
    background-color: #fff;
    border-radius: 14px;
    box-shadow: 2px 4px 16px #eaeaea;
}

.orders_container .body .car_infos .info_component.call {
    margin-top: auto;
}

.orders_container .body .car_infos h3 {
    font-size: 19px;
    line-height: 19px;
    margin-bottom: 10px;
}

.orders_container .body .car_infos .header {
    margin-bottom: 10px;
    width: 100%;

    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 20px;
}

.orders_container .body .car_infos .header .img_container {
    width: 50px;
    height: 50px;
    border-radius: 12px;
    box-shadow: 2px 4px 6px #eaeaea;
    overflow: hidden;
}

.orders_container .body .car_infos .header .img_container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.orders_table {
    width: 100%;
    max-width: 1200px;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    border: 1px solid #e1e1e1;
    border-radius: 14px;

    overflow: auto;
}

.orders_header, .order {
    padding: 5px 20px;
    width: 100%;
    
    display: grid;
    grid-template-columns: repeat(11, 1fr);
    align-items: center;
    gap: 10px;
}

.orders_header.client, .order.client {
    grid-template-columns: repeat(8, 1fr);
}

.orders_header {
    margin-bottom: 10px;
}

.order {
    border-bottom: 1px solid #e1e1e1;
}

.order:nth-last-child(1) {
    border: none;
}

.order .img_container {
    margin: 0 auto;
    width: 40px;
    height: 40px;

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 10px;
    box-shadow: 2px 4px 6px #e1e1e1;
    overflow: hidden;
}

.order .img_container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.order p,
.order a {
    font-size: 14px;
    text-align: center;
}

.order a {
    text-decoration: underline;
}

.order button {
    margin: 0 auto;
    padding: 6px 10px;
    font-size: 12px;

    border: none;
    outline: none;
    border-radius: 10px;
    box-shadow: 2px 4px 6px #eaeaea;

    cursor: pointer;
}

.order button.verified {
    background-color: #10bb00;
    color: #fff;
}

.order button.verifying {
    background-color: #fff;
    color: #000;
}

.order button.not_verified {
    background-color: #d10000;
    color: #fff;
}

.order button.delete {
    padding: 5px;
    width: 30px;
    height: 30px;

    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #d10000;
}

.order button.delete img {
    width: 100%;
}

.orders_header.six, .order.six {
    grid-template-columns: repeat(6, 1fr);
}

.orders_header {
    padding: 20px;
    border-bottom: 1px solid #000;
    background-color: #000;
    color: #fff;
}

.orders_header p {
    font-size: 14px;
    font-weight: 400;
    text-align: center;
}

.pdf__papper_container {
    margin-right: auto;
    margin-top: 20px;
    max-width: 820px;
    
    border-radius: 14px;
    background-color: #fff;
    box-shadow: 2px 4px 20px #e9e9e9;
}

.pdf__papper {
    padding: 40px;
    width: 826px;
    /* height: 1050px; */
    
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    gap: 40px;
    
    font-size: 14px;
    font-family: arial;
}

.pdf_header {
    width: 100%;
    display: grid;
    grid-template-columns: 2fr 8fr;
}

.logo_container {
    width: 100%;
    max-width: 400px;

    padding: 10px;
}

.logo_container img {
    width: 100%;
}

.agency_info {
    width: auto;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.agency_info h1 {
    font-size: 54px;
    font-weight: 500;
}

.pdf__papper table {
    width: 100%;
}

.pdf__papper table, tr, td, th {
    padding: 10px;
    height: 100%;
    max-height: 100px;

    border-collapse: collapse;
    border: 1px solid #000;
    text-align: center;
}

.download__btn__container {
    width: 100%;
}

.download__btn {
    margin: 20px auto;
    padding: 10px 40px;

    color: #fff;
    background-color: #00954B;
    border-radius: 12px;
    border: none;
    outline: none;

    cursor: pointer;
}

@media (max-width: 1080px) {
    .orders_table {
        width: 100%;
        align-items: flex-start;
        justify-content: flex-start;
    }

    .orders_header, .order {
        min-width: 1200px;
        width: 100%;
    }
}

@media (max-width: 700px) {
    .car_container .name_and_price .price_wrapper strong {
        font-size: 14px;
    }
}

@media (max-width: 690px) {
    .orders_container .body {
        flex-direction: column;
    }

    .orders_container .body .car_infos {
        max-width: 100vw;
        width: 100%;
    }

    .pdf__papper_container {
        transform-origin: top left;
        transform: scale(.5);
    }
}

@media (max-width: 640px) {
    .cars_container {
        flex-direction: column;
    }

    .car_container {
        width: 100%;
    }
}

@media (max-width: 500px) {
    .orders_container .body .car_infos {
        padding: 20px;
    }

    .orders_container .body .car_infos .header .img_container {
        width: 40px;
        height: 40px;
        border-radius: 10px;
    }

    .orders_container .header h2 {
        font-size: 19px;
    }
    
    .info_component p {
        font-size: 12px;
    }

    .orders_container .body .car_infos {
        gap: 8px;
    }
    
    .info_component .price h2 {
        font-size: 18px;
        line-height: 14px;
    }

    .info_component .order_span span,
    .info_component a {
        font-size: 12px;
    }

    .download__btn {
        width: 100%;
    }

    .orders_header, .order {
        padding: 10px;
    }

    .orders_header p {
        font-size: 12px;
    }

    .order .img_container {
        width: 33px;
        height: 33px;
    }

    .order p, .order a {
        font-size: 12px;
    }
}

@media (max-width: 375px) {
    .pdf__papper_container {
        transform: scale(.4);
    }
}

@media (max-width: 300px) {
    .orders_container {
        padding: 10px;
        gap: 0;
    }
    
    .orders_container .body .car_infos {
        padding: 15px;
    }

    .pdf__papper_container {
        transform: scale(.3);
    }

    .car_container .name_and_price .price_wrapper strong {
        font-size: 12px;
    }
}
