.car_details_container {
    margin: 70px auto 0;
    padding: 20px;
    width: 100%;
    max-width: 1400px;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;   
}

.car_details_container .header,
.orders_container .header {
    width: 100%;
    margin-bottom: 20px;
}

.orders_container .header button,
.car_details_container .header button {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 5px;
    
    font-size: 14px;
    color: #00954B;

    background-color: transparent;
    outline: none;
    border: none;
    border-bottom: 1px solid #00954B;

    cursor: pointer;
}

.orders_container .header h2,
.car_details_container .header h2 {
    font-size: 24px;
    color: #00954B;
}

.car_details_wrapper {
    padding: 40px 20px;
    width: 100%;

    display: flex;
    align-items: stretch;
    justify-content: center;
    gap: 20px;

    background-color: #fff;
    border-radius: 14px;
    box-shadow: 0px 4px 20px #e7e7e7;
}

.car_details_img {
    width: 60%;
    /* aspect-ratio: 2/1.2; */

    display: flex;
    align-items: center;
    justify-content: center;
    cursor: grab;
}

.car_details_slider {
    width: 100%;
    height: 100%;
}

.car_details_slider:active {
    cursor: grabbing;
    cursor: -moz-grabbing;
    cursor: -webkit-grabbing;
}

.car_details_img img {
    width: 100%;
}

.car_details_img_wrapper {
    width: 100%;
    aspect-ratio: 16/9;
}

.car_details_img_wrapper img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.car_details_information {
    padding: 0 20px;
    width: 50%;

    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
}

.info_component {
    width: 100%;
    
    display: flex;
    align-items: center;
    justify-content: flex-start;

}

.info_component .icon,
.info_component .order_span {
    max-width: 50px;
    width: 100%;

    display: flex;
    align-items: center;
}

.info_component .order_span {
    max-width: 100px;
    width: 100%;
}

.info_component p {
    font-size: 14px;
    font-weight: 500;
}

.info_component .price {
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
}

.info_component .price h2 {
    font-size: 24px;
    line-height: 18px;
}

.info_component .price span {
    font-size: 12px;
    line-height: 12px;
}

.car_details_information > * {
    margin-bottom: 10px;
}

.car_details_information p {
    display: block;
}

.car_details_information p.available {
    color: #00954B;
}

.car_details_information p.not_available {
    color: #950000;
}

.car_details_information a, .reserve_btn {
    margin-top: auto;
    padding: 10px 30px;
    width: auto;

    color: #fff;
    background-color: #00954B;
    border: none;
    outline: none;
    
    border-radius: 11px;
    font-size: 13px;
    cursor: pointer;
}

.reserve_form, .comments_container {
    padding: 20px;
    width: 100%;
    
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 10px;
    
    border-radius: 14px;
    box-shadow: 0px 4px 20px #e7e7e7;
}

.comments_container {
    margin-left: auto;
    padding: 20px;
    width: 100%;
    max-width: calc(40% - 30px);

    background-color: #fff;
}

.reserve_form {
    display: block;
}

.reserve_form h1 {
    margin-bottom: 20px;
}

.reserve_form input {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;

    border-radius: var(--main-radius);
    outline: none;
}

form label {
    margin-right: auto;
    margin-bottom: 5px;
    display: block;
}

.reserve_form input[type='submit'] {
    max-width: max-content;
    padding: 10px 30px;
    float: right;

    color: #fff;
    background-color: #000;
    
    border: none;
    outline: none;
    border-radius: 12px;

    overflow: hidden;
    cursor: pointer;
}

.comments_container form {
    margin-bottom: 5px;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;

    border: 1px solid #e1e1e1;
    border-radius: 12px;
    overflow: hidden;
}

.comments_container form input {
    padding: 8px 8px 8px 14px;
    width: 100%;
    border: none;
    outline: none;
}

.comments_container form input[type='submit'] {
    width: auto;
    padding: 10px 30px;

    font-size: 12px;
    color: #fff;
    background-color: #000;
    
    border: none;
    outline: none;
    border-radius: 14px 0 0 14px;
    
    cursor: pointer;
}

.comment {
    padding: 5px 10px;
    width: 100%;

    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;

    border-radius: 14px;
    background-color: #f9f9f9;
}

.comment_head {
    width: 100%;
    padding: 10px;
    padding-bottom: 0;
    
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;

}

/* .comment_head span:nth-child(n) {
    font-weight: 700;
} */

.comment_head span {
    font-size: 10px;
    opacity: 0.7;
}

.comment_head span:nth-child(1)::first-letter {
    text-transform: uppercase;
}

.comment_head .delete_btn {
    margin-left: auto;
    padding: 10px;

    width: 25px;
    height: 25px;
    
    display: flex;
    align-items: center;
    justify-content: center;
    
    border-radius: 8px;
    border: none;
    outline: none;
    
    background-color: #950000;
    box-shadow: 0px 2px 4px #b5b5b5;
    cursor: pointer;
}

.comment_head .delete_btn img {
    width: 20px;
}

.comment p {
    padding: 10px;
    font-size: 14px;
}
.comment p::first-letter {
    text-transform: uppercase;
}

@media (max-width: 920px) {
    .comments_container {
        width: 100vw;
        max-width: 100%;
        margin: 0;
    }
}

@media only screen and (max-width: 800px) {
    .car_details_container .car_details_wrapper {
        align-items: center;
        flex-direction: column;
    }

    .car_details_information {
        width: 100%;
    }

    .car_details_img {
        width: 100%;
    }
}


@media (max-width: 500px) {
    .reserve_form input[type='submit'] {
        margin-top: 10px;
        width: 100%;
        max-width: 100%;
    }
    
    .car_details_img, .car_details_information,.reserve_form, .comments_container {
        padding: 10px;
    }

    .car_details_img {
        padding: 0;
    }

    .car_details_container {
        padding: 10px;
    }
    
    .car_details_information {
        padding: 0px;
    }

    .car_details_wrapper {
        padding: 20px;
    }

    .comments_container {
        padding: 10px;
        margin-top: 0px;
    }

    .car_details_information a {
        margin-top: 10px;
        width: 100%;
        text-align: center;
    }
    
    .car_details_container .header, .orders_container .header {
        margin-bottom: 10px;
    }
}


@media (max-width: 300px) {
    .car_details_wrapper {
        padding: 10px 10px 0;
    }
}