.reserve_container {
    margin: 70px auto 0;
    padding: 40px;
    width: 100%;
    max-width: 1400px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
}

.reserve_container .header {
    width: 100%;
}

.reserve_container .header button {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 5px;
    
    font-size: 14px;

    background-color: transparent;
    outline: none;
    border: none;
    border-bottom: 1px solid #000;

    cursor: pointer;
}

.reserve_container .header h2 {
    font-size: 24px;
}

.reserve_container .body {
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 20px;
}

.reserve_container .body .car_infos {
    max-width: 400px;
    width: 100%;
    padding: 40px;

    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    gap: 12px;

    background-color: #fff;
    border-radius: 14px;
    box-shadow: 2px 4px 16px #eaeaea;
}

.car_infos .phone_btn {
    padding: 12px 30px;
    margin-top: 20px;
    margin-left: auto;
    width: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;

    background-color: #000;
    color: #fff;
    font-size: 14px;

    border-radius: 12px;
    cursor: pointer;
}

.car_infos .phone_btn img {
    filter: invert(1);
    width: 20px;
}

.orders_container .body .car_infos h3 {
    font-size: 19px;
    line-height: 19px;
    margin-bottom: 10px;
}

.reserve_container .body .car_infos .header {
    margin-bottom: 10px;
    width: 100%;

    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 20px;
}

.reserve_container .body .car_infos .header .img_container {
    width: 50px;
    height: 50px;
    border-radius: 12px;
    box-shadow: 2px 4px 6px #eaeaea;
    overflow: hidden;
}

.reserve_container .body .car_infos .header .img_container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.reserve_container .body .form_container {
    margin-right: auto;
    padding: 40px;
    max-width: 600px;
    width: 100%;
    

    background-color: #fff;
    box-shadow: 2px 4px 16px #eaeaea;
    border-radius: 14px;
}

.reserve_container .body .form_container form {
    width: 100%;

    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
}

.reserve_container .body .form_container p.error {
    font-size: 14px;
    color: #e40000;
}

.reserve_container .body .form_container form input {
    padding: 10px 20px;
    width: 100%;

    border: 1px solid #e1e1e1;
    outline: none;
    border-radius: 12px;
}

.reserve_container .body .form_container form input[type='submit'] {
    padding: 10px 30px;
    margin-left: auto;
    max-width: max-content;

    background-color: #000;
    color: #fff;

    cursor: pointer;
}

@media (max-width: 780px) {
    .reserve_container .body {
        flex-direction: column;
    }

    .reserve_container .body .car_infos {
        width: 100%;
        max-width: 100%;
    }

    .reserve_container .body .form_container {
        width: 100%;
        max-width: 100%;
    }
}

@media (max-width: 500px) {
    .reserve_container {
        padding: 20px;
    }

    .reserve_container .body .car_infos {
        padding: 20px;
    }

    .reserve_container .body .form_container {
        padding: 20px;
    }
    
    .reserve_container .body .form_container p.error {
        font-size: 12px;
    }

    .reserve_container .body .form_container form input[type='submit'] {
        max-width: 100%;
    }

    .reserve_container .header h2 {
        font-size: 17px;
    }

    .reserve_container .body .form_container form h1 {
        font-size: 17px;
    }
}

@media (max-width: 300px) {
    .reserve_container {
        padding: 10px;
    }

    .reserve_container .body .car_infos {
        padding: 15px;
    }

    .reserve_container .body .form_container {
        padding: 10px;
    }

    .reserve_container .body .form_container form input {
        padding: 10px;
        border-radius: 10px;
        font-size: 11px;
    }
}

